import { IconProp } from '@fortawesome/fontawesome-svg-core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC } from 'react';
import ScrollableComponent from '../ScrollableComponent/ScrollableComponent';
import './MenuSection.scss';

export interface IMenuSectionProps {
  children: any;
  icon: IconProp;
  id: string;
  scrollable?: boolean;
  title: string;
}

const MenuSection: FC<IMenuSectionProps> = (props: IMenuSectionProps) => {
  const getContent = (): JSX.Element => {
    if(props.scrollable) {
      return (
        <ScrollableComponent className="menu-section-content">
          {props.children}
        </ScrollableComponent>
      );
    }
  
    return (    
      <div className="menu-section-content">
        {props.children}  
      </div>
    );
  }
  
  return (
    <div id={props.id} className="menu-section">
      <div className="menu-section-title">
        <FontAwesomeIcon size="2x" color="rgba(245, 210, 145,0.6)" icon={props.icon} />
        <span className="menu-section-title-text">{props.title}</span>
      </div>
      {getContent()}
    </div>
  )
}

export default MenuSection;
